.container {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  height: 100%;
}

h1 span {
  color: #57bbf2;
}

h1 {
  color: #fff;
}

h2 {
  font-size: 1.5rem;
}

.input-style {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  border-radius: 10px;
  border: none;
  outline: 1px solid #fff;
  padding: 10px;
  color: #fff;
}

.container-input label {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 1.4rem;
  gap: .7rem;
}

.container-hexa {
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 1.4rem;
  margin-top: 1rem;
  height: 100%;
  flex-direction: column;
  gap: 3rem;
}

.btn-convert {
  color: #fff;
  border: none;
  outline: 2px solid #fff;
  border-radius: 10px;
  padding: 10px;
  font-weight: 400;
  font-size: 1.3rem;
  cursor: pointer;
}

.btn-convert:hover {
  background-color: #fff;
  color: #000;
  outline: 2px solid #000;
}

.code {
  outline: 1px solid #fff;
  border-radius: 10px;
  padding: 20px;
  color: #fff;
  font-size: 1.5rem;
}

footer {
  color: #fff;
  margin-top: 1.5rem;
}

footer a {
  text-decoration: none;
  color: #00ffff;
}

.btn-cta {
  background-color: #fff;
  padding: 13px;
  border-radius: 10px;
  font-weight: 700;
  font-size: 1.1rem;
  cursor: pointer;
}

@media (min-width: 320px) {
  .container {
    min-height: 95vh;
  }
}

@media (min-width: 425px) {
  .container {
    min-height: 99vh;
  }
}

@media (min-width: 769px) {
  .container {
    min-height: 100vh;
  }
}